.srcUser{
    width: 4.5rem;
    height: 4.5rem;
    border-radius:100px;
    background-position: center;
}
.gameover {
    width: 50vmin;
    background-size: cover;
    background-position: center;
    cursor: pointer;
  }

  .container{
      padding: 1rem;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      gap:2rem;
      flex-direction: column;
      font-family: "Ubuntu", sans-serif;
  }

  .containerStatistics{
    width: 100%;
    height: 65%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    background: rgb(27,33,85);
    background: radial-gradient(circle, rgba(27,33,85,0.5718662464985995) 0%, rgba(25,22,61,1) 100%);  border-radius: 10px;
    box-shadow: 0 5px 16px 0 rgb(0 0 0 / 58%);
  }
  .box1{
    width: 100%;
    height: 50%;
    display: flex;
    justify-content: space-evenly;
  
  }
  .boxStatisticGame,.boxStatisticUser{
      width: 48%;
      height: 100%;
      background: rgb(27,33,85);
      /* box-shadow: 0 5px 16px 0 rgb(0 0 0 / 58%); */
      background: radial-gradient(circle, rgba(27,33,85,0.5718662464985995) 0%, rgba(25,22,61,1) 100%);  border-radius: 10px;
      box-shadow: 0px 0px 7px 1px #3b3b8a;
      padding: 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }
  .boxStatisticGame p, .boxStatisticUser p{
      text-align: center;
      color: rgb(225, 223, 255);
      font-family: "Fredoka One", cursive;
   
  }
  .containerCoinMedalsQuestions{
      display: flex;
      gap:1.2rem;
      width: 100%;
      justify-content: center;
  }
  .containerCoin img, .containerPower img{
      height:6vmin;
  }
  .containerMedal img{
    height:7.5vmin;
}
  .containerCoin, .containerMedal, .containerPower{
      display:  flex;
      align-items: center;
      gap:0.5rem;
      color: white;
  }
.progress{
    display: flex;
    align-items: center;
    justify-content: center;
    gap:0.5rem;
}

  .barra{
    width: 100px;
    height: 2.5vmin;
    background-color: rgb(21, 67, 123);
    border-radius:40px;
  
}
.barrita{
    height: 2.5vmin;
    background-color: #7bcff4;
    border-bottom-left-radius: 40px;
    border-top-left-radius: 40px;

}